/* ------------------------------------------------------------------------------------------------ */
/* Content */
/* ------------------------------------------------------------------------------------------------ */
.client-content {
  margin-top: 40px;
  width: calc(100% - 205px);
  max-width: 1088px;
  margin: 50px auto;
  margin-bottom: 0;
  margin-left: 250px; }

section {
  margin-bottom: 54px;
  &:last-child {
    margin-bottom: 0; } }

.section_title {
  position: relative;
  margin-bottom: 24px;
  h3 {
    font-size: 18px;
    line-height: 32px;
    font-weight: 600; }
  .show-more {
    position: absolute;
    top: 0;
    right: 0;
    a {
      color: $text;
      &:after {
        content: ">";
        display: inline-block;
        vertical-align: middle;
        margin-left: 9px; } } } }

.client {
  &-home {
    .welcome-banner {
      padding: 60px 66px;
      background: #fff;
      border: 1px solid #EDEDED;
      box-shadow: 4px 4px 8px #6C6C6C0F;
      border-radius: 8px;
      position: relative;
      margin-bottom: 54px;
      .text {
        max-width: 510px; }
      h2 {
        font-size: 32px;
        line-height: 42px;
        font-weight: 700;
        color: $primary;
        margin-bottom: 16px;
        img {
          max-height: 38px;
          display: inline-block;
          vertical-align: middle; } }
      p {
        color: $text-light;
        font-size: 18px;
        line-height: 26px; }
      .img {
        width: 440px;
        height: 325px;
        object-fit: cover;
        position: absolute;
        top: -35px;
        right: 12px; } } } }

.progress-bar {
  background-color: transparent;
  display: block;
  margin-bottom: 0;
  .pb {
    text-align: left;
    &-block {
      display: inline-block;
      width: 100%;
      height: 12px;
      background: #FFFFFF;
      border-radius: 10px;
      position: relative;
      overflow: hidden;
      span {
        position: absolute;
        height: 12px;
        top: 0;
        left: 0;
        background: $primary;
        border-radius: 10px; } }
    .prcntg {
      display: inline-block;
      vertical-align: middle;
      font-size: 16px;
      line-height: 22px;
      font-weight: bold;
      margin-left: 8px;
      color: #4C9F70; } }
  p {
    font-size: 18px;
    line-height: 32px;
    color: $primary;
    text-align: left;
    font-weight: 600;
    margin-bottom: 0; }
  &.rest {
    .pb {
      &-block {
        span {
          background: #ffbb00; } } }
    .prcntg {
      color: #ffbb00; } } }

.card-white {
  padding: 30px 24px;
  background: #FFF;
  border: 1px solid #EDEDED;
  border-radius: 8px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  .icon {
    width: 24px;
    height: 24px;
    margin-bottom: 16px;
    img {
      display: block;
      width: 100%;
      height: 100%; } }
  .text {
    margin-bottom: 60px;
    h4 {
      font-size: 18px;
      line-height: 1.5em;
      font-weight: 600;
      margin: 4px; }
    p {
      font-size: 14px;
      line-height: 20px;
      color: #767676;
      margin-bottom: 0; } }
  .btn-white {
    padding: 8px 12px;
    font-size: 14px;
    line-height: 1.5em; } }

.prjct-card {
  padding: 30px;
  background: #F4D9D3;
  border: 1px solid #F73824;
  box-shadow: 6px 6px 8px #F7382433;
  border-radius: 8px;
  &_title {
    margin-bottom: 40px;
    .icon {
      width: 60px;
      height: 60px;
      display: inline-block;
      vertical-align: middle;
      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain; } }
    .text {
      display: inline-block;
      vertical-align: middle;
      color: $primary;
      width: calc(100% -  77px);
      margin-left: 12px;
      h4 {
        font-size: 22px;
        line-height: 36px;
        font-weight: 700;
        margin-bottom: 4px;
        color: $primary; }
      span,
      b {
        font-size: 14px;
        line-height: 20px;
        display: inline-block; }
      b {
        margin-left: 8px; } } } }
